$bg-color: #d0dcce;
$kegpercent-bg-color:$bg-color;

*, *:before, *:after {
    box-sizing: border-box;
}

html,
body {
    height:100%;
}

body {
    font-family: 'Khula', Helvetica, sans-serif;
    color: #556773;
    margin: 0;
    padding: 0;
    background-color: $bg-color;
    background: #d0dcce url('../images/bg1.jpg') 50% top fixed repeat-x;

}

h1,h2,h3,h4,h5,h6 {
    font-family: 'Bungee Hairline', sans-serif;
    font-weight: 400;
}

#content {
    height: 100%;
    overflow: hidden;
}

.container {
    width: 100%;
    padding: 0;
    height: 100%;
}
.snap-wrap{
    height: 100%;
}

.heading {
    padding: 0;
    font-weight: 600;
    font-size: 30px;
    bottom: 0;
    left: 0;
    width: 70%;
    line-height: 30px;
    position: absolute;
    color: #556773;
}

.btn-back{
    font-size: 12px;
    color: #556773;
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: auto;
    margin: 0;
    padding: 5px;
    cursor: pointer;
    transition: all .3s;
    font-family: 'Bungee Hairline', sans-serif;
    &:hover{
        background: #164453;
        color: #d0dcce;
    }
}

.logo {
    width: 400px;
    height: auto;
    margin: 70px auto 0;
    display: block;
}

.snaps-list {
    list-style: none;
    padding:0 10px 110px;
    margin: 0 auto;
    width: 100%;
    height: 90%;
    overflow: hidden;
    overflow-y: scroll;
    li {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        flex-flow: row wrap;
        margin: 0;
        padding: 10px 15px;
        background:rgba(0,0,0, .1);
        border-bottom: 2px solid rgba(0,0,0,.05);
        .snap-item{
            width:90%;
            align-self: center;
        }

        .delete-snap{
            width: 10%;
            text-align: right;
            span{
                align-self: center;
                padding:0;
                color: #9e1d88;
            }
        }
    }
}

form.add-snap {
    width: 100%;
    padding: 0 10px;
    margin: 10px auto;
      input{
        font-size: 20px;
        line-height: 0;
        background: rgba(0,0,0,.1);
        border: 0;
        padding: 10px;
        width: 75%;
        color: #d0dcce;
        cursor: pointer;
        &:focus{
            box-shadow: none;
            outline: 0;
            border: 0;
      }

        &.btn-submit{
          width: 25%;
          text-align: left;
          font-size: 20px;
          padding: 16px;
          border: none;
          line-height: 0;
          background: transparent;
          color: #556773;
          cursor: pointer;
                &:focus{
            box-shadow: none;
            outline: 0;
            border: 0;
          }
        }
       }
    }

.form-cte {
    width: 100%;
    display: flex;
    align-items: center;
    flex-flow: row wrap;
}


.snap-input {
    flex-basis: 80%
}
.snap-toggle{
}

.snap-input {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
}

.snap-value,
.edit-snap {
    width: 80%;
    font-size: 20px;
    padding: 6px;
    border: none;
    line-height: 25px;
    color: #55676F;
    align-items: center;
}

.list-item{
    .snap-value,
    .edit-snap {
        cursor: pointer;

    }
    .snap-value:hover{
        color: #164453;
    }
}


.edit-snap{
  width: 70%;
  background: rgba(0,0,0,.1);
  color: #d0dcce;
  &:focus{
    box-shadow: none;
    outline: 0;
    border: 0;
  }
}


.btn-save,
.btn-remove,
.btn-toggle{
  font-size: 15px;
      border: none;
      line-height: 0;
      background: transparent;
      color: #556773;
      cursor: pointer;
      &:focus{
        box-shadow: none;
        outline: 0;
        border: 0;
      }
}


/* blur the same background as the body */
.frosted::before {
    margin: 0;
    content: '';
    left: 0;
    right: 0;
    bottom: 0px;
    width: 100%;
    height: auto;
    background: linear-gradient(to bottom,  rgba(208,220,206, 0) 0%, #d0dcce 100%);
    filter:blur(10px);
    z-index: -2;
    position: fixed;
    height: 70px;
}

/* add a tint layer to brighten (or darken) */
.frosted::after {
    position: fixed;
    content: '';
    left: 0;
    width: 100%;
    height: 150px;
    bottom: 0px;
    background: linear-gradient(to bottom,  rgba(208,220,206, 0) 0%, #d0dcce 100%);
    z-index: -1;
}

/* The content */
.frosted {
    font-family: Avenir Next, Avenir, Verdana, sans-serif;
    font-weight: 600;
    font-size: 32px;
    color: #fff;
    position: fixed;
    margin: 0;
    padding-left: 0;
    width: 100%;
    height: 100px;
    //border: 1px solid rgba(255,255,255,0.25);
    bottom:0;
}

.loader {
    color: #556773;
    font-family: Consolas, Menlo, Monaco, monospace;
    font-weight: bold;
    font-size: 30px;
    opacity: 0.8;
    width: 100%;
    text-align: center;
    margin: 50px auto;

    span {
        display: inline-block;
        animation: pulse 0.4s alternate infinite ease-in-out;

        &:nth-child(odd) {
            animation-delay: 0.4s;
        }
    }
}

@keyframes pulse {
  to {
    transform: scale(0.8);
    opacity: 0.5;
  }
}

